-->

<template>
    <div>
        <div>
            <div>
                <div class="l-productsearch">
                    <h1 class="c-form__title"></h1>
                    <div class="c-serch">
                        <p class="c-search__title">:</p>
                        <input
                            type="text"
                            v-model="keywords"
                            placeholder=""
                            class="c-search__prefecture"
                        />
                        <p class="c-search__title">:</p>
                        <input
                            type="text"
                            v-model="budgets"
                            class="c-search__prefecture"
                        />
                    </div>
                    <div class="c-serch2">
                        :
                        <input
                            type="date"
                            :min="new Date().toISOString().split('T')[0]"
                            class="c-search__calender"
                            v-model="Days2"
                        />
                    </div>
                </div>
                <div class="c-exhibitproductlist">
                    <div class="c-productlist">
                        <ul
                            v-if="
                                product.best_by_date >= Days2 &&
                                product.bought <= 0
                            "
                            :key="product.id"
                            v-for="product in filteredProducts"
                            class="c-productlist__items"
                        >
                            <img
                                v-bind:src="product.img_path"
                                class="c-productlist__items__img2"
                            />
                            <div class="c-productlist__items__list">
                                <li>No.{{ product.id }}</li>
                                <li>:{{ product.product_name }}</li>
                                <li>:{{ product.price }}.</li>
                                <div class="c-productlist__items__bottom">
                                    <button
                                        class="c-productlist__items__bottom__area"
                                    >
                                        <a
                                            v-bind:href="`${product.id}}/shopper_productdetail`"
                                            ></a
                                        >
                                    </button>
                                </div>
                            </div>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    props: ["products"], //main.vue
    data: function () {
        return {
            keywords: "", //
            Days2: "", //
            budgets: 10000, //10000
        };
    },

    computed: {
        //
        filteredProducts: function () {
            const products = [];
            //
            for (var i in this.products) {
                var product = this.products[i];
                if (product.prefecture.indexOf(this.keywords) !== -1) {
                    if (!this.Days) {
                        products.push(product);
                    }
                }
            }
            if (products) {
                return products.filter(function (el) {
                    return el.price <= this.budgets;
                }, this);
            }
        },
    },
};
</script>
